export function useManagePage(entityName?: string) {
  const route = useRoute()

  const id = computed(() => {
    if (route.params.id === "new") {
      return null
    }

    return route.params.id.toString()
  })
  const isCreationMode = computed(() => !id.value)

  const title = computed(() => {
    if (isCreationMode.value) {
      return `Create ${entityName}`
    }

    return `Edit ${entityName}`
  })

  const backPath = computed(() => route.fullPath.replace(/\/manage\/[\w-]+\/?/, ""))

  return {
    id,
    isCreationMode,
    title,
    backPath,
  }
}
